body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    outline: none;
    box-sizing: border-box;
}

html, body{
    height: 100vh;
    background-color: #34343b !important;
    font-family: 'Eras Demi ITC', system-ui,
        -apple-system,
        "Segoe UI",
        Roboto,
        "Helvetica Neue",
        "Noto Sans",
        "Liberation Sans",
        Arial,
        sans-serif,
        "Apple Color Emoji",
        "Segoe UI Emoji",
        "Segoe UI Symbol",
        "Noto Color Emoji" !important;
    padding: 0px !important;
}

:root{
    --bs-dark-rgb: 46,46,53 !important;
}

.f-size-60{
    font-size: 60px;
}
.f-size-50{
    font-size: 50px;
}
.f-size-36 {
    font-size: 36px;
}
.f-size-20{
    font-size: 20px;
}

.f-size-15{
    font-size: 15px;
}
.w-200px{
    width: 200px;
}

.h-200px{
    height: 200px;
}
.h-175px{
    height: 175px;
}

.form-select, .form-control{
    background-color: #dadada;
    border-radius: 0px;
}

label{
    color: #dadada;
    margin-bottom: 0.5rem;
}

.multi-columns{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.p-color {
    color: #e4aa48;
}

.t-color {
    color: #dadada;
}

.s-color {
    color: #2e2e35;
}

.out-color {
    color: #0dadaa;
}

.p-back {
    background-color: #e4aa48;
}

.t-back {
    background-color: #dadada;
}

.s-back {
    background-color: #2e2e35;
}

.out-back {
    background-color: #0dadaa;
}

.text-justify{
    text-align: justify;
}

.navbar-dark{
    /* background-color: #2e2e35 !important; */
    --bs-navbar-color: rgba(218, 218, 218, 1) !important;
}

.dropdown-menu.dropdown-menu-end{
    right: 0;
    background-color: rgb(228 170 72);
}

.sticky-top{
    z-index: 9999 !important;
    border-bottom: 1px solid #e4aa48;
}

.alert-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    right : 0;
    bottom: 0;
    z-index: 9999 !important;
    background-color: rgba(256,256,256,0.2);
}

.alert-wrapper .alert{
    z-index: 9999 !important;
    margin: 80px 10px 0px 10px;
}

.modal{
    padding-top: 50px;
}

/* React Quill editor start*/

.ql-editor{
    min-height: 200px;
}

.ql-toolbar, .ql-editor {
    background-color: #dadada;
}

.ql-toolbar{
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.ql-toolbar.ql-snow .ql-formats{
    margin-right: 0px !important;
}

/* React Quill editor end*/

.sp-btn-secondary{
    background-color: #2e2e35 !important;
    border: 1px solid #e4aa48 !important;
    text-transform: uppercase !important;
    padding-top: 5px !important;
    padding-bottom: 5px !important;
    color: #e4aa48 !important;
    display: inline-block !important;
    font-weight: bold !important;
}

.sp-btn-secondary:hover{
    background-color:#e4aa48 !important;
    color: #2e2e35 !important;
}

.homebanner-image{
    background-image: url('/images/shibaprassad-profile.jpg');
    background-position: top center;
    background-repeat: no-repeat;
    background-clip: content-box;
    /* background-size: cover; */
    min-height: calc(100vh - 57px);
    /* max-height: 576px; */
}

.banner-left, .banner-right{
    width: 100%;
    max-width: 360px;
    color: white;
    text-align: center;
}

.banner-right{
    color: #e4aa48;
}

hr{
    height: 2px;
    background-color: #e4aa48;
    opacity: 1 !important;
}

.name-dot{
    display: inline-block;
    width: 15px;
    height: 15px;
    font-size: 60px;
    color: #e4aa48;
    line-height: 15px;
}

.service-category .col div, .my-skills .col div{
    transition: 0.5s all ease-in-out;
    border: 1px solid #2e2e35;
    cursor: pointer;
    color: #dadada;
    height: 100%;
}

.service-category .col div:hover, .my-skills .col div:hover {
    transition: 0.2s all ease-in-out;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background-color: #e4aa48;
    color: #2e2e35 !important;
    border-color: rgba(218,218,218,0.2);
}


.primary-button{
    background-color: #2e2e35 !important;
    color: #e4aa48 !important;
    border-radius: 0px !important;
}

.primary-button:hover{
    background-color: #e4aa48;
    color: #2e2e35;
}

.primary-link{
    color: #e4aa48 !important;
    font-weight: bold;
}

.primary-link:hover{
    color: #2e2e35 !important;
}

/*********** FOOTER ****************/
.footerback{
    background-image: url('/images/my-portfolio-1_34.jpg');
    min-height: 400px;
    margin: 40px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.socialmedia-container {
    margin: 40px 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    text-align: center;
}

.socialmedia-content{
    max-width: 900px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    grid-gap: 50px;
    gap: 50px;
    align-items: center;
}

.socialmedia-icons{
    height: 50px;
    width: 50px;
    background-size: cover;
    cursor: pointer;
    background-repeat: no-repeat;
}

.socialmedia-icons.fb{
    background-image: url("/images/icons_fb.jpg");
}
.socialmedia-icons.wt{
    background-image: url("/images/icons_wt.jpg");
}
.socialmedia-icons.li{
    background-image: url("/images/icons_li.jpg");
}
.socialmedia-icons.in{
    background-image: url("/images/icons_in.jpg");
}
.socialmedia-icons.tt{
    background-image: url("/images/icons_tt.jpg");
}

/****************BLOG********************/
.blogbanner-image img{
    width: 100%;
}

.blogTech-container{
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
}

.blogTech-items{
    background-color: #e4aa48;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
}

.blogTech-items-logoimg{
    width: 100%;
    height: 49px;
    background-position: center left;
    background-repeat: no-repeat;
    background-size: 80px 49px;
    padding-left: 80px;
}

.blogTech-items-logoimg span{
    line-height: 40px;
}

.blogTech-items-content h3{
    display: none;
}

.blogTech-item-separator{
    height: 1px;
    width: 100%;
    border-top: 1px solid #2e2e35;
}

.component-container{
    position: relative;
    width: 100%;
    min-height: calc(100vh - 56px);
    display: flex;
    flex-direction: column;
    padding-top: 45px;
}

.component-left{
    width: 100%;
    height: auto;
}

.component-left.isClose{
    display: none;
}

.component-right {
    flex: 1 1;
}

.component-left h3{
    border-bottom: 1px solid #34343b;
}

.component-left-drawer{
    display: block;
    position: absolute;
    top: 10px;
    left: 10px;
    right: 10px;
    color: #dadada;
    display: flex;
    justify-content: space-between;
}

.list-group, .list-group-item.disabled,
.list-group-item:disabled {
    --bs-list-group-bg: transparent !important;
    background-color: transparent !important;
    cursor: auto;
}

.list-group-item{
    color: #dadada;
    cursor: pointer;
}

.list-group-item:hover{
    color: #e4aa48;
}
.list-group-item:hover h4{
    color: #dadada;
}



.component-right img{
    max-width: 80%;
}


/**************************** Accordian *********************/

.accordion{
    --bs-accordion-color: #fff;
    --bs-accordion-bg: rgba(256, 256, 256, 0.02);
    --bs-accordion-active-color: #e4aa48;
    --bs-accordion-active-bg: rgba(256, 256, 256, 0.05);
}

.accordion-item{
    border-color: rgba(256, 256, 256, 0.07);
}

.accordion-item .quill{
    color: rgba(0,0,0, 0.7);
}

.accordion-button{
    color:#e4aa48;
}

.accordion-button::after{
    padding: 10px;
    border: 1px solid #fff;
    background-position: center;
    background-color: rgba(256, 256, 256, 0.5);
}

.accordion-button:focus, .accordion-button:not(.collapsed){
    box-shadow:none;
    border: 1px dashed rgba(256, 256, 256, 0.07);
}

.profile .row{
    padding: 10px 0px;
}

.profile_pic{
    border: 1px solid #e4aa48;
    padding: 5px;
    max-height: 120px;
}

.edit-icon{
    position: absolute;
    right: 10px;
    top: 5px;
    background-color: rgba(256, 256, 256, 0.2);
    padding: 10px;
    border: 1px solid rgba(256, 256, 256, 0.5);
    border-radius: 50%;
    cursor: pointer;
}

.accordion .edit-icon{
    right: 0px;
}

.edit-icon:hover {
    background-color: rgba(256, 256, 256, 0.7);
    color: rgba(0, 0, 0, 0.5)
}



@media (min-width: 576px) {
    .blogTech-items {
            flex-direction: row;
    }
    .blogTech-items-logoimg {
        height: 74px;
        width: 120px;
        background-size: 120px 74px;
    }
    .blogTech-items-logoimg span{
        display: none;
    }
    .blogTech-items-content h3 {
        display: block;
    }
    .blogTech-item-separator {
        height: unset;
        width: 1px;
        border-left: 1px solid #2e2e35;
        margin: 10px 0px;
    }
    .component-container {
        flex-direction: row;
        padding-top: 0px;
    }
    .component-left {
        max-width: 250px;
        min-height: 100%;
    }
    .component-left.isClose {
        display: block;
    }
        .component-left-drawer{
            display: none;
        }
}

